
<style scoped>
.step-box {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.next-btn {
  width: 100%;
  margin-top: 80px;
  margin-bottom: 20px;
}

.input-box {
  margin-top: 5px;
  margin-bottom: 5px;
}

.root {
  padding: 20px;
}
</style>


<template>
  <div class="root">
    <h1>注册新账号</h1>

    <el-steps :active="display.current" finish-status="success">
      <el-step title="验证Email"></el-step>
      <el-step title="设置密码"></el-step>
      <el-step title="设置昵称"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>

    <div class="step-box" v-show="display.current==display.verify">
      <h2>验证 Email</h2>

      <el-input class="input-box" v-model="email" type="email"  placeholder="填写 Email 地址"></el-input>

      <el-input class="input-box" v-model="vcode"  type="text" placeholder="验证码">
        <el-button
          slot="append"
          icon="el-icon-message"
          @click="onClickRequestVcode"
        >{{ verifyCodeButtonText }}</el-button>
      </el-input>
      <!-- div>
        点击 “获取验证码” 之后，一封包含验证码的 email 将被发送到邮箱：
        <span>{{email}}</span> ，请及时查收。
      </div-->
      <el-button type="primary" class="next-btn" @click="nextForVerifyEmail">提交验证</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.password">
      <h2>设置密码</h2>
      <el-input class="input-box" v-model="password" placeholder="输入：新密码" type="password"></el-input>
      <el-input class="input-box" v-model="password2" placeholder="重复：新密码" type="password"></el-input>
      <el-button type="primary" class="next-btn" @click="nextForSetupPassword">下一步</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.nickname">
      <h2>设置昵称</h2>
      <el-input class="input-box" v-model="nickname" placeholder="输入：昵称" type="text"></el-input>
      <el-button type="primary" class="next-btn" @click="nextForSetupNickname">下一步</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.success">
      <h2>完成</h2>
      <p>恭喜！你已经成功创建新的账号： {{email}}</p>
      <el-button type="primary" class="next-btn" @click="nextForOK">用新账号登录</el-button>
    </div>
  </div>
</template>


<script>
var sha256 = require("js-sha256").sha256;

export default {
  name: "SignUpView",

  data() {
    return {
      email: "",
      vcode: "",

      password: "",
      password2: "",
      nickname: "",
      active: 0,
      verifyCodeButtonText: "获取验证码",
      verifyCodeTimeout: 0,

      form: {
        email: "",
        code: "",
        token: "",
        nickname: "",
        password: ""
      },

      display: {
        current: 0,
        verify: 0,
        password: 1,
        nickname: 2,
        success: 3
      }
    };
  },

  methods: {
    nextForVerifyEmail() {
      var self = this;
      this.form.email = this.email;
      this.form.code = this.vcode;
      var on_ok = () => {
        self.display.current++;
      };
      this.requestVerifyVcode(on_ok);
    },

    nextForSetupPassword() {
      var password = this.verifyAndHashPassword(this.password, this.password2);
      if (password != null) {
        this.form.password = password;
        this.display.current++;
      }
    },

    nextForSetupNickname() {
      this.form.nickname = this.nickname;
      var self = this;
      var on_ok = () => {
        self.display.current++;
      };
      this.requestCreateAccount(on_ok);
    },

    nextForOK() {
      this.$router.push("/login");
    },

    onClickRequestVcode() {
      if (this.verifyCodeTimeout > 0) {
        return;
      }

      this.form.email = this.email;

      var self = this;
      this.verifyCodeTimeout = 60;
      this.verifyCodeButtonText = "";

      var on_timer = () => {
        var timeout = self.verifyCodeTimeout;
        if (timeout > 0) {
          self.verifyCodeButtonText = timeout + "秒后重试";
          setTimeout(on_timer, 1000);
        } else {
          self.verifyCodeButtonText = "获取验证码";
        }
        self.verifyCodeTimeout = timeout - 1;
      };

      this.requestSendMail();
      on_timer();
    },

    requestSendMail(on_ok, on_error) {
      var self = this;

      var code = "";
      var token = "";
      var email = this.email;

      // rest
      var type = "VerifyEmail";
      var id = null;
      var data = {
        verifyEmail: { email, token, code }
      };

      on_ok = this.handleEvent(on_ok, res => {
        var token = res.verifyEmail.token;
        self.form.token = token;
      });

      this.$store.dispatch("rest/post", { type, id, data, on_ok, on_error });
    },

    requestVerifyVcode(on_ok, on_error) {
      var code = this.form.code;
      var token = this.form.token;
      var email = this.form.email;

      // rest
      var type = "VerifyEmail";
      var id = token;
      var data = {
        verifyEmail: { email, token, code }
      };

      this.$store.dispatch("rest/put", { type, id, data, on_ok, on_error });
    },

    requestCreateAccount(on_ok, on_error) {
      var self = this;

      var secret = this.form.password;
      var email = this.form.email;
      var code = this.form.code;
      var token = this.form.token;
      var nickname = this.form.nickname;
      var mechanism = "PASSWORD";

      var type = "CreateAccount";
      var id = null;
      var data = {
        auth: { name: email, secret, mechanism },
        account: {
          entity: { email, nickname }
        },
        verifyEmail: { email, code, token }
      };

      if (on_error == null) {
        on_error = err => {
          self.showError(err);
        };
      }

      this.$store.dispatch("rest/post", { type, id, data, on_ok, on_error });
    },

    verifyAndHashPassword(pass1, pass2) {
      if (pass1 != pass2) {
        this.$message("密码不相等。");
        return null;
      } else if (pass1.length < 4) {
        this.$message("请输入有效的密码。");
        return null;
      } else {
        var pass = pass1;
        pass = sha256(pass);
        pass = sha256(pass);
        return pass;
      }
    },

    handleEvent(l1, l2) {
      var handler = res => {
        if (l2 != null) {
          l2(res);
        }
        if (l1 != null) {
          l1(res);
        }
      };
      return handler;
    },

    showError(err) {
      this.$message("Error");
    }
  }
};
</script>
